import React from "react"
import Link from "gatsby-link"

const ListLink = props =>
  <li style={{ display: `inline-block`, marginRight: `1rem` }}>
    <Link to={props.to}>
      {props.children}
    </Link>
  </li>

export default ({ children }) =>
  <div style={{ margin: `0 auto`, padding: `1.25rem 1rem` }}>
    <header style={{ marginBottom: `1.5rem`, backgroundColor: `black` }}>
      <Link to="/" style={{ textShadow: `none`, backgroundImage: `none` }}>
        <img src='http://theministryofpeculiarities.com/assets/images/white-banner-3553x521.png' style={{ display: `inline`, maxHeight: `50px`, margin: `10px` }} />
      </Link>
      <ul style={{ listStyle: `none`, float: `right`, color: `white`, margin: `20px` }}>
        <ListLink to="/">Home</ListLink>
        <ListLink to="/about/">About</ListLink>
        <ListLink to="/contact/">Contact</ListLink>
      </ul>
    </header>
    {children()}
    <footer style={{textAlign: `center`, marginTop: `1.5rem`, backgroundColor: `black`, color:`white`}}>
      <a style={{margin: '20px'}} href='https://facebook.com/ministryofpeculiarities'>facebook</a>
      <a style={{margin: '20px'}} href='https://instagram.com/ministry_of_peculiarities'>instagram</a>
      <a style={{margin: '20px'}} href='mailto:info@theministryofpeculiarities.com'>email</a>
      <p>&copy;2017 The Ministry of Peculiarities, LLC</p>
    </footer>
  </div>


